.aboutContainer{
    position: relative;
    background-color: #3D3D3D;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 1080px;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.aboutContainer > p{
    font-size: 16px;
    padding: 25px;
    color:white;
    max-width: 50%;
}
