.outerContainer{
    position: absolute;
    left: 0px;
    height: 100%;
    z-index: 100;
    width: 100%;
    background-color:rgba(0, 0, 0, 0.555);
    transition: width 0s, background-color .6s;
}
.navBar{
    display:flex;
    flex-direction: column;
    width: 80vw;
    height: 100%;
    max-width: 500px;
    background-color: #474747;
    transition: .4s;
    z-index: 110;
}

.linkContainer{
    display: flex;
    flex-direction: column;
}

.navLinks{
    padding: 20px;
    text-decoration: none;
    color: white;
    font-size: 22px;
    transition: visibility .2s, opacity .2s !important;
}

.navLinks:hover{
    background: #3d3d3dc0;
}

.topBar{
    display:flex;
    justify-content: space-between;
    align-items: center;
    transition: visibility .2s, opacity .2s !important;
}

.logo{
    min-width:100px;
    max-width: 20%;
    padding: 25px;
}

.arrow{
    cursor: pointer;
    width:36px;
    height: 36px;
    padding: 25px 20px 25px 35px;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.arrow:hover{
    background: #3d3d3dc0;
}

.menuBtn{
    cursor: pointer !important;
    position: absolute;
    left: 0px;
    width:38px;
    height: 38px;
    padding: 25px;
    align-self: flex-start;
    transition: .2s;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.menuBtn:hover{
    background: #3d3d3dc0;
}

.shown{

}

.hidden{
    opacity: 0;
    visibility: hidden;
}