.homeContainer{
    position: relative;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 1080px;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.outer{
    display:flex;
    justify-content: center;
    width:100%;
    height: 100%;
}

.cropTool{
    max-width: 1080px;
    border: 10px solid transparent;
    border-image: url("./../../../../Images/BorderImage.svg") 15% stretch;
    z-index: 90;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.video{
    height: 100vh;
    width: auto;
}

.canvas{
    position: absolute;
    width: 100vw;
    height: 100vh;
    opacity: 0;
}

.controls{
    display:flex;
    align-items: center;
    position: absolute;
    bottom: 20px;
}

.cropBtn{
    cursor:pointer;
    height: 36px;
    width: 36px;
    padding: 20px;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.uploadBtn{
    cursor:pointer;
    height: 40px;
    width: 40px;
    padding: 20px;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.captureBtn{
    cursor:pointer;
    height: 60px;
    width: 60px;
    padding: 20px;
    filter: drop-shadow( 2px 3px 4px rgba(0, 0, 0, 0.2));
}

.captureBtn:hover{
    transform: scale(1.1);
}

.uploadBtn:hover{
    transform: scale(1.1);
}

.cropBtn:hover{
    transform: scale(1.1);
}

.popUp{
    position:absolute;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    max-width: 50%;
    background-color: #474747;
    padding: 40px;
    margin: 35px 50px 35px 50px;
    z-index: 90;
    border-radius: 20px;
    box-sizing: border-box;
    transition: .8s;
}

.popUp > p {
    font-size: 18px;
    padding: 16px;
    color:white;
}

.hidePopUp{
    opacity: 0;
    visibility: hidden;
}

.dismissBtn {
    font-size: 18px;
    cursor: pointer;
    padding: 10px;
    margin: 10px;
    background-color: white;
    border: solid 1px white;
    color: #474747;
    border-radius: 16px;
    transition: .1s;
}

.dismissBtn:hover{
    color: white;
    background-color: #474747;
}