.App {
  text-align: center;
  background-color: #cccc;
}

input[type="file"] {
  display:none;
}

a:focus {
  outline: none;
}

button:focus {
  outline: none;
}

.noHighlights{
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.myHighlight{
  -webkit-tap-highlight-color:#3d3d3dc0;
}